}
.documentation-content .language-cshtml pre {
  margin: 0;
  padding: 1rem;
}
.documentation-content .language-cshtml pre code {
  display: block;
  white-space: pre;
  word-wrap: normal;
  font-family: "Source Code Pro", monospace;
  font-size: 0.95em;
  line-height: 1.5;
