.filter-panel {
  border: 1px solid #D6D9DD;
  border-radius: 5px;
  padding: 20px 16px;
}
.filter-panel h3 {
  font-family: var(--gcds-font-families-heading);
  font-style: normal;
  font-weight: 700;
  margin-bottom: 1.5rem;
}
.filter-panel .filter-section {
  margin-bottom: 1.5rem;
}
.filter-panel .filter-section .fdcp-collapse-button {
  border: none;
  background: transparent;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 16px;
  align-self: stretch;
  width: 100%;
  color: #000;
  font-family: var(--gcds-font-families-body);
  font-size: 17.778px;
  font-style: normal;
  font-weight: 500;
  position: relative;
  margin-bottom: 16px;
  padding: 0;
}
.filter-panel .filter-section .fdcp-collapse-button:after {
  content: "\f077";
  position: absolute;
  right: 0;
  font-family: "Font Awesome 6 Free";
  font-weight: 900;
}
.filter-panel .filter-section .fdcp-collapse-button[aria-expanded=true]::after {
  content: "\f078";
}
.filter-panel .filter-section .fdcp-collapse {
  display: none;
  transition: all 0.3s ease;
}
.filter-panel .filter-section .fdcp-collapse.fdcp-show {
  display: block;
}
.filter-panel .filter-section .filter-option {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
  padding-left: 0.2rem;
}
.filter-panel .filter-section .filter-option input[type=checkbox] {
  display: none;
}
.filter-panel .filter-section .filter-option input[type=checkbox] + label {
  display: flex;
  align-items: center;
  cursor: pointer;
  position: relative;
  padding-left: 1.75rem;
  font-size: 1rem;
  color: #000;
}
.filter-panel .filter-section .filter-option input[type=checkbox] + label::before {
  content: "";
  display: inline-block;
  width: 1rem;
  height: 1rem;
  border: 2px solid #333;
  border-radius: 0.2rem;
  background-color: #fff;
  position: absolute;
  left: 0;
  top: 50%;
  transform: translateY(-50%);
  transition: border-color 0.15s ease-in-out, background-image 0.15s ease-in-out, outline 0.15s ease-in-out;
}
.filter-panel .filter-section .filter-option input[type=checkbox]:checked + label::before {
  background-color: transparent;
  border-color: #1d4ed8;
  background-image: url("data:image/svg+xml,%3Csvg width='12' height='10' viewBox='0 0 12 10' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M1 5l3 3 7-7' stroke='%231d4ed8' stroke-width='2' fill='none'/%3E%3C/svg%3E");
  background-repeat: no-repeat;
  background-position: center;
}
.filter-panel .filter-section .filter-option .filter-count {
  background-color: #D7E5F5;
  color: #000;
  padding: 0.2rem 0.5rem;
  border-radius: 0.375rem;
  font-size: 0.875rem;
  font-weight: 600;
  min-width: 1.5rem;
  text-align: center;
  width: 2rem;
}

.fdcp-badge {
  position: relative;
  display: inline-flex;
  padding: 0.25rem 0.75rem;
  border-radius: 1.875rem;
  white-space: nowrap;
  font-size: 0.875rem;
  line-height: 1.25rem;
  font-weight: 500;
  vertical-align: middle;
  gap: 0.25rem;
}
.fdcp-badge .fdcp-badge-start,
.fdcp-badge .fdcp-badge-end {
  display: inline-flex;
  align-items: center;
